<template>
  <v-container>
    <v-row justify="center">
      <v-card class="card-usr" max-width="600">
        <v-card-title class="card-head-usr">{{ this.userSettings['name'].value }} 様の登録情報</v-card-title>
        <v-card-text class="card-body-usr">
          <v-table fixed-header>
            <tbody>
              <tr v-for="(item, key) in userSettings">
                <td class="list-title">{{ `${item.title} :` }}</td>
                <td class="list-value">{{ item.value }}</td>
                <td></td>
                <td v-if="item.editable">
                  <v-btn icon="mdi-pencil-outline" color="blue" size="x-small" @click="beginEdit(key)"></v-btn>
                </td>
              </tr>
            </tbody>
          </v-table>
        </v-card-text>
      </v-card>
    </v-row>

    <!-- 登録情報再設定ダイアログ -->
    <v-dialog v-model="showEditDialog" max-width="500" attach>
      <v-card>
        <v-card-title>{{ editInfo.title }}を再設定します</v-card-title>
        <v-card-text>
          <v-row>
            <v-col cols="4">
              <v-list-subheader>現在の設定:</v-list-subheader>
            </v-col>
            <v-col class="mt-2" cols="8" justify="center">
              <p>{{ editInfo.beforeValue }}</p>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="4">
              <v-list-subheader>再設定値:</v-list-subheader>
            </v-col>
            <v-col cols="8">
              <v-text-field
                v-model.trim="editInfo.afterValue"
                density="compact"
                :type="getInputType"
                :maxlength="userSettings[editInfo.key].maxLen"
                :append-inner-icon="getAppendIcon"
                @click:append-inner="visiblePass = !visiblePass"
                :error="!!errMsg"
                :error-messages="errMsg"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row v-if="editInfo.key === 'pass'">
            <v-col cols="4">
              <v-list-subheader>再確認:</v-list-subheader>
            </v-col>
            <v-col cols="8">
              <v-text-field
                v-model="editInfo.afterValueConf"
                density="compact"
                :type="visiblePassConf ? 'text' : 'password'"
                :maxlength="userSettings[editInfo.key].maxLen"
                :append-inner-icon="visiblePassConf ? 'mdi-eye-off-outline' : 'mdi-eye-outline'"
                @click:append-inner="visiblePassConf = !visiblePassConf"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-card-text>
        <v-card-actions>
          <v-btn color="primary" @click="closeEditDialog()">閉じる</v-btn>
          <v-btn
            class="usr-button"
            text="確認画面へ"
            append-icon="mdi-account-edit-outline"
            @click="confirmSetting"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- 登録情報再設定確認ダイアログ -->
    <v-dialog v-model="showConfirmDialog" max-width="500" attach>
      <v-card>
        <v-card-title>{{ editInfo.title }}再設定確認</v-card-title>
        <v-card-text>
          <v-row>
            <v-col class="list-title" cols="4">
              <v-list-subheader>現在の設定:</v-list-subheader>
            </v-col>
            <v-col class="mt-2" cols="8" justify="center">
              <p>{{ editInfo.beforeValue }}</p>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="4">
              <v-list-subheader>再設定値:</v-list-subheader>
            </v-col>
            <v-col class="mt-2" cols="8">
              <p v-if="editInfo.key === 'pass'">＊＊＊＊＊＊＊＊</p>
              <p v-else>{{ editInfo.afterValue }}</p>
            </v-col>
          </v-row>
        </v-card-text>
        <v-card-actions>
          <v-btn color="primary" @click="showConfirmDialog = false">キャンセル</v-btn>
          <v-spacer></v-spacer>
          <v-btn
            class="usr-button"
            text="設定する"
            append-icon="mdi-account-edit-outline"
            @click="registerSetting"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- 編集完了ダイアログ -->
    <v-dialog v-model="showCompleteDialog" max-width="400">
      <v-card prepend-icon="mdi-check-circle-outline" title="変更完了">
        <v-card-text>
          変更が完了しました。
        </v-card-text>
        <v-card-actions>
          <v-btn color="primary" @click="showCompleteDialog = false">閉じる</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import { computed } from 'vue';

export default {
  name: "AccountInfo",
  created() {
    // TODO : バックエンドからユーザ設定を取得する
    this.userSettings['id'].value = 'h00001';
    this.userSettings['name'].value = '山田 太郎';
    this.userSettings['nameKana'].value = 'ヤマダ タロウ';
    this.userSettings['mail'].value = 'taro_yamada@xxx.test';
    this.userSettings['phone'].value = '090-1111-1111';
    this.userSettings['pass'].value = '＊＊＊＊＊＊＊＊';
  },
  computed: {
    getInputType() {
      if (this.userSettings[this.editInfo.key].type === 'password') {
        return (this.visiblePass ? 'text' : 'password');
      } else {
        return this.userSettings[this.editInfo.key].type;
      }
    },
    getAppendIcon() {
      if (this.userSettings[this.editInfo.key].type === 'password') {
        return (this.visiblePass ? 'mdi-eye-off-outline' : 'mdi-eye-outline');
      } else {
        return null;
      }
    }
  },
  data() {
    return {
      userSettings: {
        id        : { title: 'ID', value: '', editable: false, type: 'text', maxLen: 0},
        name      : { title: '氏名', value: '', editable: false, type: 'text', maxLen: 0},
        nameKana  : { title: '氏名(カナ)', value: '', editable: false, type: 'text', maxLen: 0},
        mail      : { title: 'メールアドレス', value: '', editable: true, type: 'email', maxLen: 255},
        phone     : { title: '電話番号', value: '', editable: true, type: 'text', maxLen: 20},
        pass      : { title: 'パスワード', value: '', editable: true, type: 'password', maxLen: 64},
      },
      visiblePass: false,
      visiblePassConf: false,
      errMsg: '',
      showEditDialog: false,
      editInfo: {
        key: '',
        title: '',
        beforeValue: '',
        afterValue: '',
        afterValueConf: '',
      },
      showConfirmDialog: false,
      showCompleteDialog: false,
    }
  },
  methods: {
    beginEdit(key) {
      this.errMsg = '';
      this.editInfo.key = key;
      this.editInfo.title = this.userSettings[key].title;
      this.editInfo.beforeValue = this.userSettings[key].value;
      this.editInfo.afterValue = '';
      this.editInfo.afterValueConf = '';

      this.showEditDialog = true;
    },
    closeEditDialog() {
      this.showEditDialog = false;
      this.visiblePass = false;
      this.visiblePassConf = false;
    },
    confirmSetting() {
      // 入力チェック
      this.errMsg = '';
      switch(this.editInfo.key) {
        case 'mail':
          const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
          if (!emailRegex.test(this.editInfo.afterValue)) {
            this.errMsg = '正しいメールアドレスを入力してください';
          }
          break;
        case 'phone':
          const phoneRegex = /^[0-9\-]*$/;
          if (!phoneRegex.test(this.editInfo.afterValue)) {
            this.errMsg = '数字とハイフンのみ入力可能です';
          }
          break;
        case 'pass':
          if (this.editInfo.afterValue.length < 8) {
            this.errMsg = 'パスワードは8文字以上で入力してください';
            break;
          }
          if (this.editInfo.afterValue !== this.editInfo.afterValueConf) {
            this.errMsg = '「再確認」の入力と一致しません';
            break;
          }
          // TODO : 利用可能文字の制限を追加する
          break;
        default:
          break;
      }
      if (this.errMsg !== '') {
        return;
      }

      this.showEditDialog = false;
      this.showConfirmDialog = true;
    },
    registerSetting() {
      this.showConfirmDialog = false;
      this.showCompleteDialog = true;
    },
  },
};
</script>

<style scoped>
.list-title {
  text-align: right;
  vertical-align: middle;
}
.list-value {
  text-align: left;
}
</style>
