  <v-container class="pt-10">
    <v-slide-x-transition mode="out-in">
      <v-row v-if="currentView === 'clientInfo'" key="clientInfo">
        <ClientInfo/>
      </v-row>
      <v-row v-else-if="currentView === 'reservation'" key="reservation">
        <Reservation :current-view="currentView" @switch-view="switchView"/>
      </v-row>
      <v-row v-else-if="currentView === 'history'" key="history">
